
*{
  margin: 0px;
  padding: 0px;
}

.desc {
  width: 98%;
  margin: auto;
  padding-top: 20px;
  text-align: left;
  height: 180px;
}
h2{
  color: #fff;
}
.love_base{
width: 100vw;
height: 100vh;  
background: rgb(239, 158, 197);
overflow: hidden;
}
.snow{
            background: #fff;
            position: absolute;
            width: 10px;
            height: 10px;
            border-radius: 50%;
}
.love_bomm{
  width: 100%;
  height: calc(100vh - 200px);
  position: relative;
 -webkit-animation:fadenum 16s 1;
         animation:fadenum 16s 1;
 /* animation:fadenum 1s 1; */
}
@-webkit-keyframes fadenum{
0%{opacity: 0;
}
50%{opacity: 0;
}
100%{opacity: 1;
}
}
@keyframes fadenum{
0%{opacity: 0;
}
50%{opacity: 0;
}
100%{opacity: 1;
}
}
.love_bomm_img{
  width: 90%;
  margin: auto;
  height: 400px;
  position: absolute;
  left: 0px;
  top: 0px;
}
.love_bomm_wz{
  width: 90%;
  margin: auto;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  position: absolute;
  left: 0px;
  top: 127px;
}
.love_bomm_wz span{
  color: red;
  font-size: 20px;
}
.love_q{
  width: 88%;
  margin: auto;
  text-align: right;
  position: absolute;
  bottom: 150px;
  left: 0px;
  color: #fff;
}
.active{
  color: red;
}

.typed-element {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.typed-element .typed-cursor {
    opacity: 1;
    -webkit-animation: typedjsBlink 0.7s infinite;
            animation: typedjsBlink 0.7s infinite;
}
@-webkit-keyframes typedjsBlink {
50% {
    opacity: 0.0;
}
}
@keyframes typedjsBlink {
50% {
    opacity: 0.0;
}
}
